import { Component, OnInit, NgZone, ChangeDetectorRef } from '@angular/core';
import { AuthService } from './service/auth.service'
import { ClassService } from './service/class.service'
import { slideInAnimation } from './animations';
import { ActivatedRoute, Router, RouterOutlet } from '@angular/router';
import { DeviceDetectorService } from 'ngx-device-detector';
declare var loop;
import { CacheService } from './service/cache.service';
@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.css'],
    animations: [slideInAnimation]
})
export class AppComponent implements OnInit {
    status: any = 1;
    userInfo;
    authorizeCount: any = 0;
    versionTimeout;
    device;
    showUpdate: boolean = false;
    constructor(private cd: ChangeDetectorRef, public cache: CacheService, public _ngZone: NgZone, private route: Router, public auth: AuthService, private classService: ClassService, private deviceService: DeviceDetectorService) {
        const deviceInfo = this.deviceService.getDeviceInfo();
        this.device = deviceInfo.os;
    }
    ngOnInit() {
        //
        // sessionStorage.setItem("classRoomUserId", '0bf877a6fcc03a1cd011fbc78343a1af8057aea0cde0c59b6c81f6d802774f8f');//guoli
        // // sessionStorage.setItem("classRoomUserId", '05ed9603e1f6edab7fdb06fadb61865c0671150574cf252267eabbcfff2f8e05');//dp
        // this.status = 2;
        // let payload = {
        //     loopUid: '0bf877a6fcc03a1cd011fbc78343a1af8057aea0cde0c59b6c81f6d802774f8f',
        //     loopUserName: 'Peng Du'
        // }
        // this.authorize(payload);
        setTimeout(() => {
            loop.ready((status) => {
                loop.setScrollEnabled(0);
                this.getuser();
            })
        }, 500);
    }
    getuser() {
        let that = this;
        loop.getUserInfo({
            param: { appName: 'Campus Groups', appId: '16e1ef02-6bb5-406d-a67e-c97ee6d55856' }, //prod
            // param: { appName: 'gl-class', appId: '3544571e-7b68-4352-86a9-29871266563e' },//gluat
            // param: { appName: 'dp-class', appId: '2544571e-7b68-4352-86a9-29871266563c' },//dpuat
            error: (err) => {

            },
            success: (resp) => {
                let _resp;
                if (typeof resp == 'string') {
                    _resp = JSON.parse(resp);

                } else {
                    _resp = resp;
                }
                if (_resp && _resp.code == 1) {
                    this.status = 2;
                    that.userInfo = _resp;
                    let id = _resp.data.id;
                    let Length = 64 - id.length;
                    if (id && id.length < 64) {
                        for (let i = 0; i < Length; i++) {
                            id += '0';
                        }
                    }
                    let loopUserId = id;
                    let loopUserName = _resp.data.name;
                    sessionStorage.setItem("classRoomUserId", id);
                    that.auth.userInfo = _resp.data;
                    let payload = {
                        loopUid: loopUserId,
                        loopUserName: loopUserName
                    }
                    this.authorize(payload);
                    this._ngZone.run(() => { });
                    this.cd.detectChanges();
                    //
                    loop.getVersion((version) => {
                        if (version) {
                            let _version = JSON.parse(version);
                            if (_version.code == 1) {
                                let data = _version.data;
                                if (data.platform == 'iOS') {
                                    this.showUpdate = this.checkVersion(data.version, '1.0.4');
                                    this._ngZone.run(() => { });
                                    this.cd.markForCheck();
                                } else {
                                    this.showUpdate = this.checkVersion(data.version, '0.0.57');
                                    this._ngZone.run(() => { });
                                    this.cd.markForCheck();
                                }
                            } else {
                                //
                                this.showUpdate = true;
                                this._ngZone.run(() => { });
                            }
                        }
                    })

                } else if (_resp && _resp.code == 0) {
                    this.status = 3;
                    that._ngZone.run(() => { });
                } else if (_resp && _resp.code == 2) {
                    this.status = 4;
                    that._ngZone.run(() => { });
                }
                this._ngZone.run(() => { });
            }
        });

    }

    reLogin() {
        this.getuser();
    }

    authorize(payload) {
        if (this.authorizeCount > 3) {
            return;
        }
        this.authorizeCount++;
        this.auth.sendPost('user', payload).subscribe(resp => {
            if (resp && resp.hasOwnProperty('status') && resp.status == 200) {
                let _body = resp.body;
                if (_body.code == 2000) {
                    this.cache.userInfo = _body.payload;
                    this._ngZone.run(() => { });
                    if (!this.cache.userInfo.schoolId) {
                        // this.route.navigateByUrl('search-school');
                        this._ngZone.run(() => { });
                    } else {
                        // this.route.navigateByUrl('search-class');
                        this._ngZone.run(() => { });
                    }
                }
            } else {
                setTimeout(() => {
                    this.authorize(payload)
                }, 500)
            }
            this._ngZone.run(() => { });
        })
    }
    prepareRoute(outlet: RouterOutlet) {
        return outlet && outlet.activatedRouteData && outlet.activatedRouteData['animation'];
    }

    checkVersion(current, need) {
        let _c = current.split('.');
        let _n = need.split('.');
        let c = parseInt(_c);
        let n = parseInt(_n);
        if (_c[0] < _n[0] || (_c[0] == _n[0] && _c[1] < _n[1]) || (_c[0] == _n[0] && _c[1] == _n[1] && _c[2] < _n[2])) {
            return true;
        } else {
            return false;
        }
    }
    toUpdate() {
        if (this.device == 'iOS') {
            loop.openUrlBySafari('https://itunes.apple.com/app/id1457815876');
        } else {
            loop.openUrl('https://play.google.com/store/apps/details?id=com.qhtech.loopchat&hl=en_US');
        }

    }
    callback(data) {
        this.cache._showGroups = 'move';
        setTimeout(() => {
            this.cache.showGroups = false;
        }, 250)
    }
    groupInfocallback(data) {
        this.cache._showGroupInfo = false;
        setTimeout(() => {
            this.cache.showGroupInfo = false;
        }, 250)
    }
    createcallback(data) {
        this.cache._showCreateGroup = 'move';
        setTimeout(() => {
            this.cache.showCreateGroup = false;
        }, 250)
    }

    mySchoolCallback(data) {
        this.cache._showMySchool = 'move';
        setTimeout(() => {
            this.cache.showMySchool = false;
        }, 250)
    }
}
