    userInfo;
    schoolImage;
    constructor(private classService: ClassService, private auth: AuthService, private cache: CacheService, private router: Router,private myGroups: MyGroupsService, public schoolService: SchoolService) {
        this.userInfo = auth.userInfo;
        if(this.userInfo && this.userInfo.bio) {
            this.userInfo.bio = this.userInfo.bio.replace(/\n/g,'<br>');
        }
    }
    ngOnInit() {
        this.groups = this.myGroups.myGroups;
        this.myGroups.getAllMyGroups([], 1, this.cache.userInfo.schoolId, (getAllDone) => {
            if(getAllDone) {
                this.groups = this.myGroups.myGroups;
            }
        });
        if(!this.schoolService.userSchool || !this.schoolService.userSchool.schoolImage) {
            this.auth.sendGet('file?id=' + this.schoolService.userSchool.icon).subscribe(resp => {
                if (resp && resp.hasOwnProperty('status') && resp.status == 200) {
                    let _body = resp.body;
                    if (_body.code == 2000) {
                       this.schoolService.userSchool.schoolImage = _body.payload;
                    }
                }else {
                    this.auth.error('Network timeout, please try again later')
                }
            }) 
        }
        
    }
