import { Injectable } from '@angular/core';
import { AuthService } from './auth.service';

@Injectable({
    providedIn: 'root'
})
export class MyGroupsService {
    myGroups;
    constructor(private auth: AuthService) { }
    getAllMyGroups(groups, page, schoolId, callback) {
        if (!groups) { groups = [] };
        const url = 'page=' + page + '&pageSize=100'+ '&schoolId=' + schoolId;
        this.auth.sendGet('user/group/list?' + url).subscribe(resp => {
            if (resp && resp.hasOwnProperty('status') && resp.status == 200) {
                let _body = resp.body;
                if (_body && _body.hasOwnProperty('code') && _body.code == 2000) {
                    let datas = _body.payload;
                    for (let data of datas) {
                        groups.push(data);
                    }
                    if (datas && datas.length == 100) {
                        page++;
                        this.getAllMyGroups(groups, page, schoolId, callback)
                    } else {
                        this.myGroups = groups;
                        callback(true);
                    }
                }
            } else {
                this.auth.error('Network timeout, please try again later')
            }
        })
    }
}
