import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { CacheService } from '../../service/cache.service';
import { SchoolService } from '../../service/school.service';
import { AuthService } from '../../service/auth.service';
import { ClipboardService } from 'ngx-clipboard';
declare var loop;
@Component({
    selector: 'app-group-detail',
    templateUrl: './group-detail.component.html',
    styleUrls: ['./group-detail.component.css']
})
export class GroupDetailComponent implements OnInit {
    groupInfo;
    @Output() callback: EventEmitter<any> = new EventEmitter<any>();
    constructor(private cache: CacheService, public schoolService: SchoolService, private auth: AuthService) {
        this.groupInfo = cache.selectedGroup;
    }

    ngOnInit() {
        if (!this.schoolService.userSchool.schoolImage) {
            this.auth.sendGet('file?id=' + this.schoolService.userSchool.icon).subscribe(resp => {
                if (resp && resp.hasOwnProperty('status') && resp.status == 200) {
                    let _body = resp.body;
                    if (_body.code == 2000) {
                        this.schoolService.userSchool.schoolImage = _body.payload;
                    }
                } else {
                    this.auth.error('Network timeout, please try again later')
                }
            })
        }
    }
    goBack() {
        this.callback.emit({ type: 'close' });
    }
    joinGroup(link) {
        if(!link) {
            alert('The group has been dissolved!');
            return;
        }
        // loop.openUrl('https://loopchat.us/invitation/1' + link);
        // loop.joinGroup('1'+link, (data) => {
        //     alert(data);
        //     if(data && data.code == '0') {
        //         this.auth.error('The group has been dissolved')
        //     }
        // })
        loop.getVersion((version) => {
            if (version) {
                let _version = JSON.parse(version);
                if (_version.code == 1) {
                    let data = _version.data;
                    let status;
                    if (data.platform == 'iOS') {
                        status = this.checkVersion(data.version, '1.0.9');
                    } else {
                        status = this.checkVersion(data.version, '0.1.52');
                    }
                    if (!status) {
                        loop.joinGroup('1' + link, (data) => {
                            if (data && data.code == '0') {
                                this.auth.error('The group has been dissolved');
                            }
                        })
                    } else {
                        loop.openUrl('https://loopchat.us/invitation/1' + link);
                    }
                } else {
                }
            }
        })
    }
    commitStatus: any = '';
    copy() {
        this.commitStatus = 'success';
        setTimeout(() => {
            this.commitStatus = '';
        }, 500)

    }
    checkVersion(current, need) {
        let _c = current.split('.');
        let _n = need.split('.');
        let c = parseInt(_c);
        let n = parseInt(_n);
        if (_c[0] < _n[0] || (_c[0] == _n[0] && _c[1] < _n[1]) || (_c[0] == _n[0] && _c[1] == _n[1] && _c[2] < _n[2])) {
            return true;
        } else {
            return false;
        }
    }
}
