import { Component, OnInit } from '@angular/core';
import { ClassService } from '../../service/class.service';
import { CacheService } from '../../service/cache.service';
import { AuthService } from '../../service/auth.service';
import { SchoolService } from '../../service/school.service';
import { InterestListService } from '../../service/interest-list.service';
import { Router } from '@angular/router';
@Component({
    selector: 'app-interests-groups',
    templateUrl: './interests-groups.component.html',
    styleUrls: ['./interests-groups.component.css']
})
export class InterestsGroupsComponent implements OnInit {
    groups;
    page;
    getAllDone: boolean = false;
    lastDataLength;
    loading: boolean = false;
    constructor(public schoolService: SchoolService, private classService: ClassService, private auth: AuthService, private cache: CacheService, private router: Router, private interestListService: InterestListService) {
        this.page = 1;
        this.interestListService.cacheInterestGroups = [];
        interestListService.getInterestList(this.page,this.cache.userInfo.schoolId,datas => {
            this.groups = datas;
        })
    }

    ngOnInit() {
        this.auth.sendGet('school/get?id=' + this.cache.userInfo.schoolId).subscribe(resp => {
            if (resp && resp.hasOwnProperty('status') && resp.status == 200) {
                let _body = resp.body;
                if (_body && _body.hasOwnProperty('code') && _body.code == 2000) {
                    this.schoolService.userSchool = _body.payload;
                }
            } else {
                this.auth.error('Network timeout, please try again later')
            }
        });
    }
    getInterestGroups(page) {
        this.page = page;
        this.lastDataLength = this.groups ? this.groups.length : 0;
        const url = 'page=' + page + '&pageSize=10' + '&schoolId=' + this.cache.userInfo.schoolId;
        this.auth.sendGet('group/list?' + url).subscribe(resp => {
            if (!this.groups) {
                this.groups = [];
            }
            if (resp && resp.hasOwnProperty('status') && resp.status == 200) {
                let _body = resp.body;
                if (_body && _body.hasOwnProperty('code') && _body.code == 2000) {
                    let datas = _body.payload;
                    this.getAllDone = datas.length == 10 ? false : true;
                    for (let data of datas) {
                        this.groups.push(data);
                    }
                }
            } else {
                this.auth.error('Network timeout, please try again later')
            }
        })
    }
    loved(event, group) {
        if (event == 'clicked') {
            group.likeNum++;
        }
    }
    searchMore(event) {
        let box = event.target;
        if(this.loading) {
            return;
        }
        if (box.scrollHeight - box.offsetHeight - box.scrollTop < 80) {
            this.page++;
            this.loading = true;
            this.interestListService.getInterestListByPage(this.page, this.cache.userInfo.schoolId, resp => {
                this.getAllDone = resp.getAllDone;
                if(this.getAllDone && resp.datas.length <= 0) {
                    this.page--;
                    this.interestListService.page--;
                }
                for (let data of resp.datas) {
                    this.groups.push(data);
                }
                this.loading = false;
            })
        }
    }

    showGroupDetail(group) {
        this.cache.selectedGroup = group;
        this.cache.showGroupInfo = true;
        this.cache._showGroupInfo = true;
    }
}
