import { Component, OnInit, NgZone, ChangeDetectorRef, AfterViewInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { AuthService } from '../../service/auth.service';
import { SchoolService } from '../../service/school.service';
import { CacheService } from '../../service/cache.service';
import { ActivatedRoute, Router, RouterOutlet } from '@angular/router';
@Component({
    selector: 'app-select-school',
    templateUrl: './select-school.component.html',
    styleUrls: ['./select-school.component.css']
})
export class SelectSchoolComponent implements OnInit, AfterViewInit {
    selectedSchool;
    schoolList;
    userSchool;
    constructor(
        private cd: ChangeDetectorRef,
        public _ngZone: NgZone,
        private auth: AuthService,
        public schoolService: SchoolService,
        private cache: CacheService,
        private router: Router,
        private title: Title) {
        this.selectedSchool = this.schoolService.userSchool;
        let payload = {
            currentPage: 1,
            pageSize: 1000
        }
        this.getSchoolList(payload);
    }

    ngOnInit() {

    }
    ngAfterViewInit() { }
    getSchoolList(payload) {
        let url = `school/list?page=${payload.currentPage}&pageSize=${payload.pageSize}`;
        this.auth.sendGet(url).subscribe(resp => {
            if (resp && resp.hasOwnProperty('status') && resp.status == 200) {
                let _body = resp.body;
                if (_body.code == 2000) {
                    this.schoolService.schoolList = _body.payload;
                    this.schoolList = _body.payload;
                    for (const school of this.schoolService.schoolList) {
                        this.schoolService.schoolMap[school.id] = school;
                        if (this.cache.userInfo && this.cache.userInfo.schoolId && this.cache.userInfo.schoolId == school.id) {
                            this.schoolService.userSchool = school;
                            this.userSchool = school;
                        }
                    }
                    if (!this.selectedSchool) {
                        this.selectedSchool = this.schoolService.schoolList[0]
                    }
                    this.cd.detectChanges();
                }
            }
        })
    }
    selectSchool(school) {
        this.selectedSchool = school;
        this.cd.detectChanges();
    }
    searchStr;
    search(event) {
        if (event && event.keyCode == 13 && this.searchStr) {
            this.auth.sendGet(`school/search?page=1&pageSize=1000&name=${this.searchStr}`).subscribe(resp => {
                if (resp && resp.hasOwnProperty('status') && resp.status == 200) {
                    let _body = resp.body;
                    if (_body.code == 2000) {
                        this.schoolService.schoolList = _body.payload;
                        this.schoolService.schoolMap = {};
                        for (const school of this.schoolService.schoolList) {
                            this.schoolService.schoolMap[school.id] = school;
                        }
                    }
                }

            })

        } else if (event && event.keyCode == 13 && !this.searchStr) {
            let payload = {
                currentPage: 1,
                pageSize: 1000
            }
            this.getSchoolList(payload);
        }
    }
    confirm() {
        this.auth.sendPut('user', { schoolId: this.selectedSchool.id }).subscribe(resp => {
            if (resp && resp.hasOwnProperty('status') && resp.status == 200) {
                let _body = resp.body;
                if (_body.code == 2000) {
                    this.cache.userInfo = resp.body.payload;
                    this.schoolService.userSchool = this.selectedSchool;
                    if(this.selectedSchool) {
                        this.title.setTitle(this.selectedSchool.name.trim().replace(/^\S/, s => s.toUpperCase()) + ' Groups');
                    }
                    this.router.navigateByUrl("/search-class");
                }
            }
        });
    }
}
