import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../service/auth.service';
import { CacheService } from '../../service/cache.service';
import { SchoolService } from '../../service/school.service';
import { Title } from '@angular/platform-browser';
@Component({
    selector: 'app-my-school',
    templateUrl: './my-school.component.html',
    styleUrls: ['./my-school.component.css']
})
export class MySchoolComponent implements OnInit {
    currentpage: any = 1;
    pageSize: any = 1000;
    schoolList: any = [];
    selectedSchool: any;

    staticSchoolList: any = [];
    disabled: boolean = false;
    commitStatus: any = 'init';
    height: any = 0;
    constructor(
        private auth: AuthService, 
        private cache: CacheService, 
        private schoolService: SchoolService,
        private title: Title) { }

    ngOnInit() {
        this.getSchoolList(1, this.pageSize);
        this.height = document.body.clientHeight- 140;
    }

    goBack() {
        history.go(-1);
    }

    getSchoolList(page, pageSize) {
        this.auth.sendGet(`school/list?page=${page}&&pageSize=${pageSize}`).subscribe(resp => {
            if (resp && resp.hasOwnProperty('status') && resp.status == 200) {
                let _body = resp.body;
                if (_body && _body.hasOwnProperty('code') && _body.code == 2000) {
                    this.staticSchoolList = _body.payload;
                    this.schoolList = this.staticSchoolList;
                    for (let school of this.schoolList) {
                        if (school.id == this.cache.userInfo.schoolId) {
                            this.selectedSchool = school;
                            school.isSelected = true;
                        }
                    }
                }
            }
        })
    }

    selectSchool(school) {
        this.selectedSchool.isSelected = false;
        school.isSelected = true;
        this.selectedSchool = school;
    }
    School: any = "";
    schoolChange(event) {
        if (!event) {
            this.schoolList = this.staticSchoolList;
        } else {
            this.schoolList = [];
            let str = event.toLowerCase().trim();
            for (let school of this.staticSchoolList) {
                if (school.name.toLowerCase().indexOf(str) > -1) {
                    this.schoolList.push(school);
                }
            }
        }
    }
    submitEdit() {
        this.disabled = true;
        
        this.auth.sendPut('user',{schoolId: this.selectedSchool.id}).subscribe(resp => {
            this.disabled = false;
            if (resp && resp.hasOwnProperty('status') && resp.status == 200) {
                let _body = resp.body;
                if (_body.code == 2000) {
                    this.schoolService.userSchool = this.selectedSchool;
                    this.cache.userInfo = resp.body.payload;
                    if(this.selectedSchool) {
                        this.title.setTitle(this.selectedSchool.name.trim().replace(/^\S/, s => s.toUpperCase()) + ' Groups');
                    }
                    this.commitStatus = 'success';
                    setTimeout(() => {
                        this.commitStatus = 'init';
                        this.goBack();
                    }, 1500)
                }
            }else {
                this.auth.error('Network timeout, please try again later')
            }
        });
    }
}
