import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, RouterOutlet } from '@angular/router';
import { slideInAnimation } from '../../animations'
import { CacheService } from '../../service/cache.service';
// import {Observable} from 'rxjs';
// import {map} from 'rxjs/operators';
@Component({
    selector: 'app-main',
    templateUrl: './main.component.html',
    styleUrls: ['./main.component.css']
})
export class MainComponent implements OnInit {
    navigate = 'interests';
    lhp: boolean = false;
    showConfirm: boolean = false;
    constructor(public cache: CacheService, private router: Router, private activatedRoute: ActivatedRoute) {
        // const url = this.activatedRoute.url.subscribe(url => console.log(url));
        if(window.screen.availHeight/window.screen.availWidth > 2) {
            this.lhp = true;
        }
    }

    ngOnInit() {
        this.navigate = this.cache.currentNavigate;
    }
    navigateTo(route) {
        
        if(!this.cache.userInfo.schoolId) {
            this.showConfirm = true;
            return;
        }
        this.navigate = route;
        this.cache.currentNavigate = route;
        let url = route == 'home' ? 'search-class' : route == 'interests' ? 'interests-groups' : 'my-groups-list'
        this.router.navigateByUrl(url);
    }

    prepareRoute(outlet: RouterOutlet) {
        return outlet && outlet.activatedRouteData && outlet.activatedRouteData['animation'];
    }

}
