import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { CacheService } from '../../service/cache.service';
import { AuthService } from '../../service/auth.service';
import { GroupListService } from '../../service/group-list.service';

@Component({
    selector: 'app-class-group-list',
    templateUrl: './class-group-list.component.html',
    styleUrls: ['./class-group-list.component.css']
})
export class ClassGroupListComponent implements OnInit {
    classInfo;
    groups;

    //
    page;
    getAllDone: boolean = false;
    lastDataLength;
    loading: boolean = false;
    @Output() callback: EventEmitter<any> = new EventEmitter<any>();
    constructor(private router: Router, private cache: CacheService, private auth: AuthService, private groupList: GroupListService) {
        this.classInfo = cache.selectedClass;
        // this.page = groupList.page;
        this.page = 1;
        this.groupList.cacheGroups = [];
        this.groupList.cachePage = 1;
        // this.getMyGroups(1);
        this.groupList.getGroupsByClassID(this.classInfo.id, this.page, this.cache.userInfo.schoolId, (datas) => {
            this.groups = datas;
        })
    }

    ngOnInit() {

    }
    getMyGroups(page) {
        this.page = page;
        this.lastDataLength = this.groups ? this.groups.length : 0;
        const url = 'group/list?id=' + this.classInfo.id + '&type=CLASS&page=' + page + '&pageSize=10' + '&schoolId=' + this.cache.userInfo.schoolId;
        this.auth.sendGet(url).subscribe(resp => {
            if (!this.groups) {
                this.groups = [];
            }
            if (resp && resp.hasOwnProperty('status') && resp.status == 200) {
                let _body = resp.body;
                if (_body && _body.hasOwnProperty('code') && _body.code == 2000) {
                    let datas = _body.payload;
                    this.getAllDone = datas.length == 10 ? false : true;
                    for (let data of datas) {
                        this.groups.push(data);
                    }
                }
            } else {
                this.auth.error('Network timeout, please try again later')
            }
        })
    }
    goBack() {
        this.callback.emit({ type: 'close' });
    }
    showGroupDetail(group) {
        this.cache.selectedGroup = group;
        this.cache.showGroupInfo = true;
        this.cache._showGroupInfo = true;
    }
    create() {
        this.cache.selectedClass = this.classInfo;
        this.cache.createStatus = 'groupList';
        this.cache.showCreateGroup = true;
        this.cache._showCreateGroup = 'show';
        // this.router.navigateByUrl("/create-group");
    }
    loved(event, group) {
        if (event == 'clicked') {
            group.likeNum++;
        }
    }
    searchMore(event) {
        let box = event.target;
        if (this.loading) {
            return;
        }
        if (box.scrollHeight - box.offsetHeight - box.scrollTop < 200) {
            this.page++;
            this.loading = true;
            this.groupList.getGroupsByClassIDAndPage(this.classInfo.id, this.page, this.cache.userInfo.schoolId, (resp) => {
                this.getAllDone = resp.getAllDone;
                if (this.getAllDone && resp.datas.length <= 0) {
                    this.page--;
                    this.groupList.page--;
                }
                for (let data of resp.datas) {
                    this.groups.push(data);
                }
                this.loading = false;
            });
        }
    }

}
