import { Component, OnInit, NgZone } from '@angular/core';
import { ClassService } from '../../service/class.service';
import { CacheService } from '../../service/cache.service';
import { AuthService } from '../../service/auth.service';
declare var loop;
declare var $;
@Component({
    selector: 'app-create-interest-group',
    templateUrl: './create-interest-group.component.html',
    styleUrls: ['./create-interest-group.component.css']
})
export class CreateInterestGroupComponent implements OnInit {
    commitStatus: any = 'init';
    selectedGroup;
    selectedGroupError: boolean = false;
    description;
    descriptionError: boolean = false;
    maxCount: any = 200;
    inputCount: any = 200;
    courseList: any = [];
    courseTimeOut;
    disabled: boolean = false;
    constructor(public _ngZone: NgZone, private classService: ClassService, private auth: AuthService, private cache: CacheService) {
        
    }

    ngOnInit() {
    }

    selectGroup() {
        loop.selectMyCreatedGroup((resp) => {
            let _resp = JSON.parse(resp);
            if (_resp.code == 1) {
                this.selectedGroup = _resp.data;
                this._ngZone.run(() => { });
            }
        })
    }
    descriptionChange(event) {
        this.inputCount = this.maxCount - event.length;
        if (this.inputCount <= 0) {
            setTimeout(() => {
                this.description = this.description.substr(0, this.maxCount);
                this.inputCount = 0;
            }, 100)
        } else {
            this.inputCount = this.maxCount - event.length;
            if (this.inputCount <= 0) {
                this.inputCount = 0;
            }
        }
    }
    goBack() {
        history.go(-1);
    }

    submitCreate() {
        if(!this.selectedGroup) {
            this.selectedGroupError = true;
            return;
        }
        if(!this.description || this.description.replace(/\s/g,"") == "") {
            this.descriptionError = true;
            this.description = "";
            return;
        }
        this.disabled = true;
        const payload = {
            name: this.selectedGroup.groupName,
            notice: this.description,
            loopGroupId: this.selectedGroup.groupId,
            schoolId: this.cache.userInfo.schoolId
        }
        this.auth.sendPost('interest/group', payload).subscribe(resp => {
            this.disabled = false;
            if (resp && resp.hasOwnProperty('status') && resp.status == 200) {
                let _body = resp.body;
                if (_body && _body.hasOwnProperty('code') && _body.code == 2000) {

                    this.commitStatus = 'success';
                    setTimeout(() => {
                        this.commitStatus = 'init';
                        history.go(-1);
                    }, 1500)
                }
            } else {
                this.auth.error('Network timeout, please try again later')
            }
        })
        // this.auth.sendPost('')
    }
    closeDescriptionError() {
        this.descriptionError = false;
        var txt1 = document.getElementById("description");
        txt1.focus();
    }
}
