import { Component, OnInit, NgZone } from '@angular/core';
import { Router } from '@angular/router';
import { CacheService } from '../../service/cache.service';
import { SchoolService } from '../../service/school.service';
import { AuthService } from '../../service/auth.service';
declare var loop;
@Component({
    selector: 'app-connect-group-course',
    templateUrl: './connect-group-course.component.html',
    styleUrls: ['./connect-group-course.component.css']
})
export class ConnectGroupCourseComponent implements OnInit {
    description;
    descriptionError: boolean = false;
    groupError: boolean = false;
    choosedGroup: any;
    commitStatus: any;
    disabled: boolean = false;
    constructor(private router: Router, private _ngZone: NgZone, public cache: CacheService, public schoolService: SchoolService, private auth: AuthService) {
    }

    ngOnInit() {
        if (!this.schoolService.userSchool.schoolImage) {
            this.auth.sendGet('file?id=' + this.schoolService.userSchool.icon).subscribe(resp => {
                if (resp && resp.hasOwnProperty('status') && resp.status == 200) {
                    let _body = resp.body;
                    if (_body.code == 2000) {
                        this.schoolService.userSchool.schoolImage = _body.payload;
                    }
                } else {
                    this.auth.error('Network timeout, please try again later')
                }
            })
        }
    }
    toChoose() {
        loop.selectMyCreatedGroup((resp) => {
            let _resp = JSON.parse(resp);
            if (_resp.code == 1) {
                this.choosedGroup = _resp.data;
                this._ngZone.run(() => { });
            }
        })
    }
    submit() {
        if (!this.description || this.description.trim() == '') {
            this.descriptionError = true;
            // this.description = '';
        } else {
            this.descriptionError = false;
        }
        if (!this.choosedGroup || !this.choosedGroup.groupName || !this.choosedGroup.groupId) {
            this.groupError = true;
        } else {
            this.groupError = false;
        }
        if (this.descriptionError || this.groupError) { return; }
        this.disabled = true;
        const payload = {
            schoolId: this.cache.userInfo.schoolId,
            classId: this.cache.courseToAdd.id,
            name: this.choosedGroup.groupName,
            notice: this.description,
            loopGroupId: this.choosedGroup.groupId,
        }
        this.auth.sendPost('class/group', payload).subscribe(resp => {
            if (resp && resp.hasOwnProperty('status') && resp.status == 200) {
                let _body = resp.body;
                if (_body.code == 2000) {
                    this.commitStatus = 'success';
                    setTimeout(() => {
                        this.commitStatus = '';
                        this.disabled = false;
                        this.toMyGroup();
                    }, 1500)
                } else if (_body.code == 4003) {
                    this.commitStatus = 'groupError';
                    setTimeout(() => {
                        this.disabled = false;
                        this.commitStatus = '';
                    }, 1500)
                } else {
                    this.disabled = false;
                }
            } else {
                this.disabled = false;
                this.auth.error('Network timeout, please try again later')
            }
        })
    }
    goBack() {
        history.go(-2);
        this.cache.courseToAdd = {};
    }
    toMyGroup() {
        this.cache.currentNavigate = 'settings'
        this.router.navigateByUrl('/my-groups-list')
    }
    descriptionChange(event) {
        if (this.description.trim() != '') {
            this.descriptionError = false;
        }
        if (this.description.length > 200) {
            this.auth.error('Can only enter up to 200 characters.');
            setTimeout(() => {
                this.description = this.description.substring(0, 200);
            });
        }
    }
}
