import { Router, ActivatedRoute } from '@angular/router';
import { Component, OnInit, AfterViewInit, NgZone } from '@angular/core';
import { ClassService } from '../../service/class.service';
import { CacheService } from '../../service/cache.service';

@Component({
    selector: 'app-class-info',
    templateUrl: './class-info.component.html',
    styleUrls: ['./class-info.component.css']
})
export class ClassInfoComponent implements OnInit, AfterViewInit {
    inputStr: any = '';
    searchStr: any = '';
    classes;
    page: any = 1; //
    searchDone: boolean = true; //class
    lastScrollNum;
    searchTimeout;
    constructor(
        private cache: CacheService, 
        private router: Router, 
        private route: ActivatedRoute, 
        private classService: ClassService,
        private zone: NgZone) {
        this.searchStr = this.cache.searchStr;
        this.inputStr = this.searchStr;
        //
        this.getSearchedClasses(1);
    }

    ngOnInit() {
        setTimeout(() => {
            let inputEl = document.getElementById('classInfoInput');
            inputEl.focus();
        }, 200)
    }
    ngAfterViewInit() {
        // ...
    }
    search(event) {
        if (event && event.keyCode == 13 && this.inputStr) {
            if(this.searchTimeout) {
                return;
            }
            this.searchStr = this.inputStr;
            this.cache.searchStr = this.inputStr;
            this.reset();
            this.getSearchedClasses(1);
            // this.reset();
            // this.getSearchedClasses(1);
        }
    }
    reset() {
        this.classes = null;
        this.page = 1;
        this.searchDone = true;
        this.lastScrollNum = null;
    }
    delaySearch() {
        if(this.searchTimeout) {
            clearTimeout(this.searchTimeout);
        }
        this.searchTimeout = setTimeout(() => {
            this.searchStr = this.inputStr;
            this.cache.searchStr = this.inputStr;
            this.reset();
            this.getSearchedClasses(1);
            this.searchTimeout = null;
        }, 500)
    }
    getSearchedClasses(page) {
        this.page = page;
        this.lastScrollNum = this.classes ? this.classes.length : 0;
        let schoolId = this.cache.userInfo ? this.cache.userInfo.schoolId : '';
        this.classService.getClassList(page, 10, this.searchStr, schoolId, (data) => {
            if (!this.classes || page == 1) {
                this.classes = [];
            }
            if (data.length == 10) {
                this.searchDone = false;
            } else {
                this.searchDone = true;
            }
            for (let _class of data) {
                _class._abbreviation = this.html2Escape(_class.abbreviation);
                _class._courseNumber = this.html2Escape(_class.courseNumber);
                _class._title = this.html2Escape(_class.title);
                let tarr = this.replaceOne(this.searchStr, _class._title);
                if (tarr.length > 0) {
                    for (let replaceWord of tarr) {
                        _class._title = _class._title.split(replaceWord).join('<span class="search-reg">' + replaceWord + '</span>')
                    }
                }
                //tihuan title

                let abbarr = this.replaceOne(this.searchStr, _class._abbreviation);
                if (abbarr.length > 0) {
                    for (let replaceWord of abbarr) {
                        _class._abbreviation = _class._abbreviation.split(replaceWord).join('<span class="search-reg">' + replaceWord + '</span>')
                    }
                }
                let carr = this.replaceOne(this.searchStr, _class._courseNumber);
                if (carr.length > 0) {
                    for (let replaceWord of carr) {
                        _class._courseNumber = _class._courseNumber.split(replaceWord).join('<span class="search-reg">' + replaceWord + '</span>')
                    }
                }

                // var escapeRegExp = /[\-#$\^*()+\[\]{}|\\,.?\s]/g;
                // var escapeReg = reg => reg.replace(escapeRegExp, '\\$&');
                // var groupLeft = '<span class="search-reg">';
                // var groupRight = '</span>';
                // var groupReg = new RegExp(escapeReg(groupRight + groupLeft), 'g');
                // var groupExtractReg = new RegExp('(' + escapeReg(groupLeft) + '[\\s\\S]+?' + escapeReg(groupRight) + ')', 'g');
                // var kr = this.KeyReg(this.searchStr);

                // if (kr.regexp.test(_class.abbreviation)) {
                //     //result
                //     _class._abbreviation = _class.abbreviation.replace(kr.regexp, kr.replacement)
                //         .replace(groupReg, '');
                // }
                // if (kr.regexp.test(_class.courseNumber)) {
                //     //result
                //     _class.courseNumber = _class.courseNumber.replace(kr.regexp, kr.replacement)
                //         .replace(groupReg, '');
                // }
                // if (kr.regexp.test(_class.title)) {
                //     //result
                //     _class._title = _class.title.replace(kr.regexp, kr.replacement)
                //         .replace(groupReg, '');
                // }
                this.classes.push(_class);
                
            }
        })
    }
    //
    filterKeyword(keyword) {
        // if (replaceMent) {
        //     let replaceMentArr = replaceMent.split('');
        //     for (let i = 0; i< replaceMentArr.length; i++) {
        //         if(replaceMentArr[i] && keyword.indexOf(replaceMentArr[i]) > -1) {
        //             replaceMentArr[i] = '<span class="search-reg">' + replaceMentArr[i] + '</span>';
        //         }
        //     }
        //     return replaceMentArr.join('');
        // }
        keyword.replace(/\s+/g, "")
        var arr = [];
        for (var a = 0; a < keyword.length - 1; a++) {
            for (var i = a; i < keyword.length - 1; i++) {

                var strcache = ""
                for (var j = a; j < i + 2; j++) {
                    strcache += keyword[j] ? keyword[j] : '';
                }

                arr.push(strcache)
            }
        }
        //arr
        arr.sort((a, b) => b.length - a.length);
        return arr;
    }
    //
    replaceOne(keyword, replaceMent) {
        let filteredWordArr = this.filterKeyword(keyword);
        for (let filteredWord of filteredWordArr) {
            let upperKey = filteredWord.toUpperCase();
            let upperReplaceMent = replaceMent.toUpperCase();
            if (upperReplaceMent.indexOf(upperKey) > -1) {
                var re = new RegExp(filteredWord, 'gi')
                let arr = replaceMent.match(re);
                return arr ? this.delRepeat(arr) : [];
            }
        }
        return [];
    }
    // 
    delRepeat(arrs) {
        let map = {};
        let _arr = [];
        for (let arr of arrs) {
            if (map[arr]) {
                map[arr] = arr;
            } else {
                _arr.push(arr);
                map[arr] = arr;
            }
        }
        return _arr;
    }
    deleteInputStr() {
        this.inputStr = '';
        this.delaySearch();
    }
    goBack() {
        this.router.navigateByUrl('/search-class')
    }
    toClassGroups(_class) {
        this.cache.selectedClass = _class;
        this.cache._showGroups = 'show';
        this.cache.showGroups = true;
        // this.router.navigateByUrl('class-group-list')
    }
    searchMore(event) {
        if (this.lastScrollNum == this.classes.length || this.searchDone) {
            return;
        }
        if (event.target.scrollHeight - event.target.offsetHeight - event.target.scrollTop < 80) {
            if (!this.searchDone) {
                this.page++;
                this.getSearchedClasses(this.page);
            }
        }
    }

    KeyReg(key) {
        var escapeRegExp = /[\-#$\^*()+\[\]{}|\\,.?\s]/g;
        var src = ['(.*?)('];
        var _src;
        var ks = key.split('');
        if (ks.length) {
            while (ks.length) {
                src.push(ks.shift().replace(escapeRegExp, '\\$&'), ')(.*?)(');
            }
            src.pop();
        }
        src.push(')(.*?)');
        _src = src.join('');
        var reg = new RegExp(_src, 'gi');
        var replacer = [];
        var start = key.length;
        var begin = 1;
        while (start > 0) {
            start--;
            replacer.push('$', begin, '<span class="search-reg">$', begin + 1, '</span>');
            begin += 2;
        }
        replacer.push('$', begin);

        var info = {
            regexp: reg,
            replacement: replacer.join('')
        };
        return info;
    }
    loved(event, _class) {
        if (event == 'clicked') {
            _class.likeNum++;
        }
    }
    callback(data) {
        this.cache._showGroups = 'move';
        setTimeout(() => {
            this.cache.showGroups = false;
        }, 250)
    }
    groupInfocallback(data) {
        this.cache._showGroupInfo = false;
        setTimeout(() => {
            this.cache.showGroupInfo = false;
        }, 250)
    }
    gorCreate() {
        this.router.navigateByUrl('/add-course')
    }
    html2Escape(sHtml) {
        return sHtml.replace(/[<>&"]/g, function (c) {
            return { '<': '&lt;', '>': '&gt;', '&': '&amp;', '"': '&quot;' }[c];
        });
    }
}
