import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { AuthService } from './auth.service';
import { Observable } from 'rxjs/Rx';

@Injectable()
export class CacheService {
    userInfo;
    selectedClass;
    selectedGroup;
    selectedClassGroup;
    selectedInterestGroup;
    currentNavigate: any = 'interests';
    searchStr: any = '';

    popularClass;
    showGroups: boolean = false;
    _showGroups: any = '';
    createStatus: any = '';
    showGroupInfo: boolean = false;
    _showGroupInfo: boolean = false;
    showCreateGroup: boolean = false;
    _showCreateGroup: any = '';

    showMySchool: boolean = false;
    _showMySchool: any = '';

    courseToAdd: any = {};
    constructor(private http: HttpClient, private auth: AuthService) { }

    getCachePopularClasses(callBack) {
        this.auth.sendGet('class/popular?page=' + 1 + '&pageSize=' + 20).subscribe(resp => {
            if (resp && resp.hasOwnProperty('status') && resp.status == 200) {
                let _body = resp.body;
                if (_body && _body.hasOwnProperty('code') && _body.code == 2000) {
                    this.popularClass = _body.payload;
                    // for (let data of _body.payload) {
                    //     this.popularClass.push(data);
                    // }
                    callBack(_body.payload)
                }
            }
        })
    }
}
