import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ClassService } from '../../service/class.service';
import { AuthService } from '../../service/auth.service';
import { timeout } from 'rxjs/operators';

declare var LikeHeart;
declare var $;
@Component({
    selector: 'app-like',
    templateUrl: './like.component.html',
    styleUrls: ['./like.component.css']
})
export class LikeComponent implements OnInit {
    heartList: any = [];
    heartCount = 0;
    ctx;
    @Input() id;
    @Input() group;
    @Input() class;
    @Input() type;
    @Output() loved: EventEmitter<any> = new EventEmitter<any>();
    _setInterval;
    _timeout;
    _requestAnimationFrame: boolean = false;
    constructor(private classService: ClassService, private auth: AuthService) { }

    ngOnInit() {
        setTimeout(() => {
            // let that = this;
            // that.heartList = [];
            // // var stage = new BubbleHearts();
            // let _canvas = <HTMLCanvasElement>document.getElementById(this.id);
            // if (!_canvas) { return; }
            // this.ctx = _canvas.getContext('2d'),
            //     this.ctx.canvas.width = 200;
            // this.ctx.canvas.height = 50;
            // that.ctx.clearRect(0, 0, 200, 50);
            // that.heartList.forEach(function (item) {
            //     item && item.move(that.ctx);
            // });


            // setInterval(function () {
            //     that.heartList.push(that.createHeart());
            // }, 200);
        }, 500)
    }
    liked(event) {
        // this._setInterval = setInterval(() => {

        // },200);
        let that = this;
        this._requestAnimationFrame = false;
        event.preventDefault();
        event.stopPropagation();
        setTimeout(() => {
            this._requestAnimationFrame = true;
            (function forloop() {
                that.ctx.clearRect(0, 0, 200, 50);
                that.heartList.forEach(function (item) {
                    item && item.move(that.ctx);
                });
                if (that._requestAnimationFrame) {
                    requestAnimationFrame(forloop);
                }
            }());
            if (this._timeout) {
                clearTimeout(this._timeout);
            }
            if (this.group && this.group.id) {
                this.auth.sendPost('group/like', { id: this.group.id }).subscribe(resp => {
                    if (resp && resp.hasOwnProperty('status') && resp.status == 200) {
                        let _body = resp.body;
                        if (_body && _body.hasOwnProperty('code') && _body.code == 2000) {
                            this.loved.emit('clicked');
                            this.heartList.push(this.createHeart());
                            this._timeout = setTimeout(() => {
                                this.heartList = [];
                                this._requestAnimationFrame = false;
                            }, 2000)
                            this.ctx.clearRect(0, 0, 200, 50);
                            this.heartList.forEach((item) => {
                                item && item.move(this.ctx);
                            });
                        }
                    } else {
                        this.auth.error('You can only give 50 likes at most.')
                    }
                })
            } else if (this.class && this.class.id) {
                this.auth.sendPost('class/like', { id: this.class.id }).subscribe(resp => {
                    if (resp && resp.hasOwnProperty('status') && resp.status == 200) {
                        let _body = resp.body;
                        if (_body && _body.hasOwnProperty('code') && _body.code == 2000) {
                            this.loved.emit('clicked');
                            this.heartList.push(this.createHeart());
                            this._timeout = setTimeout(() => {
                                this.heartList = [];
                                this._requestAnimationFrame = false;
                            }, 2000)
                            this.ctx.clearRect(0, 0, 200, 50);
                            this.heartList.forEach((item) => {
                                item && item.move(this.ctx);
                            });
                        }
                    } else {
                        this.auth.error('You can only give 50 likes at most.')
                    }
                })
            }
        }, 100)
    }
    stopliked() {
        if (this._setInterval) {
            clearInterval(this._setInterval);
        }
    }
    createHeart() {
        let that = this;
        this.heartCount++;
        let positionArray = [{
            x: 100,
            y: 50,
            endX: 200,
            endY: 0
        }];
        let img = new Image();
        // img.src = './assets/images/' + Math.ceil(Math.random() * 5) + '.png';
        img.src = './assets/images/berkeley/groups-icon2@3x.png';

        // let p1 = {
        //     x: 100 + this.getRandomDis(),
        //     y: 100 + this.getRandomDis()
        // };

        // let p2 = {
        //     x: 100 + this.getRandomDis(),
        //     y: 100 + this.getRandomDis()
        // };
        let p1 = {
            x: 100,
            y: 25 + this.getRandomDis()
        };

        let p2 = {
            x: 100,
            y: 25 + this.getRandomDis()
        };
        return new LikeHeart({
            id: this.heartCount,
            x: positionArray[0].x,
            y: positionArray[0].y,
            endX: positionArray[0].endX,
            endY: positionArray[0].endY,
            onFadeOut: function removeItem(item) {
                var array = [];
                for (var i = 0; i < that.heartList.length; i++) {
                    if (that.heartList[i].id !== item.id) {
                        array.push(that.heartList[i]);
                    }
                }
                that.heartList = [];
                that.heartList = array;
            },
            // noAngel: true,
            // noScale: true,
            width: 33,
            height: 33,
            image: img,
            bezierPoint: {
                p0: {
                    x: positionArray[0].x,
                    y: positionArray[0].y
                },

                p1: p1,
                p2: p2,
                p3: {
                    x: positionArray[0].endX,
                    y: positionArray[0].endY
                }
            }
        });

    }

    getRandomDis() {
        // if (Math.random() > 0.5) {
        //     return -(Math.random() * 20);
        // } else {
        //     return +(Math.random() * 20);
        // }
        if (Math.random() > 0.5) {
            return -(Math.random() * 20);
        } else {
            return +(Math.random() * 20);
        }
    }


    removeItem(item) {
        var array = [];
        for (var i = 0; i < this.heartList.length; i++) {
            if (this.heartList[i].id !== item.id) {
                array.push(this.heartList[i]);
            }
        }
        this.heartList = [];
        this.heartList = array;
    }
    likedTwo(event) {
        event.preventDefault();
        event.stopPropagation();
        let mmm = $('#' + this.id);
        if (this.group && this.group.id) {
            this.auth.sendPost('group/like', { id: this.group.id }).subscribe(resp => {
                if (resp && resp.hasOwnProperty('status') && resp.status == 200) {
                    let _body = resp.body;
                    if (_body && _body.hasOwnProperty('code') && _body.code == 2000) {
                        this.loved.emit('clicked');
                        this.tipsBox({
                            obj: mmm,  //jqhtml
                            str: "+1",  //;html: "<b style='font-family:Microsoft YaHei;'>+1</b>"
                            startSize: "12px",  //
                            endSize: "20px",    //
                            interval: 1000,  //
                            color: "red",    //
                        });
                    }
                } else {
                    this.auth.error('You can only give 50 likes at most.')
                }
            })
        } else if (this.class && this.class.id) {
            this.auth.sendPost('class/like', { id: this.class.id }).subscribe(resp => {
                if (resp && resp.hasOwnProperty('status') && resp.status == 200) {
                    let _body = resp.body;
                    if (_body && _body.hasOwnProperty('code') && _body.code == 2000) {
                        this.loved.emit('clicked');
                        this.tipsBox({
                            obj: mmm,  //jqhtml
                            str: "+1",  //;html: "<b style='font-family:Microsoft YaHei;'>+1</b>"
                            startSize: "12px",  //
                            endSize: "20px",    //
                            interval: 1000,  //
                            color: "red",    //
                        });
                    }
                } else {
                    this.auth.error('You can only give 50 likes at most.')
                }
            })
        }
    }
    tipsBox(options) {

        $("body").append("<span class='num'>" + `<img src="./assets/images/berkeley/groups-icon2@3x.png" style="width:100%;">` + "</span>");
        var box = $(".num");
        var left = options.obj.offset().left + options.obj.width() / 2;
        var top = options.obj.offset().top;
        box.css({
            "position": "absolute",
            "left": left + "px",
            "top": top + "px",
            "z-index": 9999,
            "width": options.startSize,
            "line-height": options.endSize,
            "color": options.color
        });
        box.animate({
            "width": options.endSize,
            "opacity": "0",
            "top": top - parseInt(options.endSize) * 2 + "px",
            "left": left + 40 + "px"
        }, options.interval, function () {
            box.remove();
            // options.callback();
        });
    }
}
