import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { AuthService } from './auth.service'
import { Observable } from 'rxjs/Rx';

@Injectable({
    providedIn: 'root'
})
export class ClassService {
    constructor(private http: HttpClient, private auth: AuthService) { }
    getClassList(page, page_size, name, schoolId, callBack) {
        this.auth.sendGet('class/search?page=' + page + '&pageSize=' + page_size + '&name=' + name + '&schoolId=' + schoolId).subscribe(resp => {
            if (resp && resp.hasOwnProperty('status') && resp.status == 200) {
                let _body = resp.body;
                if (_body && _body.hasOwnProperty('code') && _body.code == 2000) {
                    callBack(_body.payload);
                }
            }
        })
    }

    getPopularClasses(page, page_size, schoolId, callBack) {
        this.auth.sendGet('class/popular?page=' + page + '&pageSize=' + page_size + '&schoolId=' + schoolId).subscribe(resp => {
            if (resp && resp.hasOwnProperty('status') && resp.status == 200) {
                let _body = resp.body;
                if (_body && _body.hasOwnProperty('code') && _body.code == 2000) {
                    callBack(_body.payload)
                }
            }
        })
    }
    groupLike(group_id, callBack) {
        this.auth.sendPost('group/like', { id: group_id }).subscribe(resp => {
            callBack(resp);
        })
    }
}
