import { Pipe, PipeTransform } from '@angular/core';
@Pipe({ name: 'likeNumPipe' })

export class likeNumPipe implements PipeTransform {
    constructor() { }

    transform(value): any {
        if(!value) {
            return 0;
        }
        let num;
        if (value === 0) {
            num = '';
        }
        else if (value > 99999 && value <= 9999999) {
            num = value / 1000;
            num = num.toFixed(1) + "K";
        }
        else if (value > 9999999) {
            num = value / 1000000;
            num = num.toFixed(1) + "M";
        } else {
            num = value;
        }
        return num;
    }
}
