import { Injectable } from '@angular/core';
import { AuthService } from './auth.service';
@Injectable({
    providedIn: 'root'
})
export class GroupListService {
    groupsMap: any = {};
    page: any = 1;
    cacheGroups: any = []
    cachePage: any = 1;
    constructor(private auth: AuthService) { }
    // init
    getGroupsByClassID(id, page, schoolId, callback) {
        // if (this.groupsMap[id]) {
        //     callback(this.groupsMap[id])
        // } else {
        this.groupsMap[id] = [];
        const url = 'class/group/list?classId=' + id + '&page=' + 1 + '&pageSize=10' + '&schoolId=' + schoolId;
        this.auth.sendGet(url).subscribe(resp => {
            if (resp && resp.hasOwnProperty('status') && resp.status == 200) {
                let _body = resp.body;
                if (_body && _body.hasOwnProperty('code') && _body.code == 2000) {
                    let datas = _body.payload;
                    this.groupsMap[id] = _body.payload;
                    // if (this.cachePage < page) {
                    //     this.cachePage++;
                    //     this.page = this.cachePage;
                    //     this.getGroupsByClassID(id, page, () => { });
                    // } else {
                    this.cachePage = 1;
                    this.groupsMap[id] = this.cacheGroups;
                    callback(_body.payload);
                    // }
                }
            } else {
                this.auth.error('Network timeout, please try again later')
            }
        })
        // }
    }
    // 
    getGroupsByClassIDAndPage(id, page, schoolId, callback) {
        this.page = page;
        const url = 'class/group/list?classId=' + id + '&page=' + page + '&pageSize=10' + '&schoolId=' + schoolId;
        this.auth.sendGet(url).subscribe(resp => {
            if (!this.groupsMap[id]) {
                this.groupsMap[id] = [];
            }
            if (resp && resp.hasOwnProperty('status') && resp.status == 200) {
                let _body = resp.body;
                if (_body && _body.hasOwnProperty('code') && _body.code == 2000) {
                    let datas = _body.payload;
                    let getAllDone = datas.length == 10 ? false : true;
                    for (let data of datas) {
                        this.groupsMap[id].push(data);
                    }
                    callback({
                        getAllDone: getAllDone,
                        datas: datas
                    })
                }
            } else {
                this.auth.error('Network timeout, please try again later')
            }
        })
    }
}
