import { Component, OnInit } from '@angular/core';
import { AuthService } from "../../service/auth.service";
import { CacheService } from "../../service/cache.service";
import { InterestListService } from "../../service/interest-list.service";
@Component({
    selector: 'app-search-interest-group',
    templateUrl: './search-interest-group.component.html',
    styleUrls: ['./search-interest-group.component.css']
})
export class SearchInterestGroupComponent implements OnInit {
    interestGroupList;
    page: any = 1;
    inputStr: any = '';
    searchDone: boolean = true;
    lastScrollNum;
    searchTimeout;
    lastDataLength;
    constructor(private auth: AuthService, private interestListService: InterestListService, private cache: CacheService) { }

    ngOnInit() {
        this.init();
    }
    goBack() {
        history.go(-1);
    }
    toClassGroups(group) {
        this.cache.selectedGroup = group;
        this.cache.showGroupInfo = true;
        this.cache._showGroupInfo = true;
    }
    init() {
        this.interestListService.searchInterestList(1, this.cache.userInfo.schoolId, this.inputStr.trim(), (datas) => {
            this.interestGroupList = datas || [];
            if (datas.length == 10) {
                this.searchDone = false;
            } else {
                this.searchDone = true;
            }
            for (let group of this.interestGroupList) {
                group._name = this.getStaticText(group.name);
            }
        })
    }
    searchMore(event) {
        let box = event.target;
        if (box.scrollHeight - box.offsetHeight - box.scrollTop < 80 && this.interestGroupList && this.lastDataLength != this.interestGroupList.length) {
            if (!this.searchDone) {
                this.lastDataLength = this.interestGroupList ? this.interestGroupList.length : 0;
                this.page++;
                this.interestListService.searchInterestListByPage(this.page, this.cache.userInfo.schoolId, this.inputStr.trim(), (datas) => {
                    if (datas.length == 10) {
                        this.searchDone = datas.getAllDone;
                    } else {
                        this.searchDone = datas.getAllDone;
                    }
                    for (let group of datas.datas) {
                        group._name = this.getStaticText(group.name);
                        this.interestGroupList.push(group);
                        
                    }
                    
                })
            }
        }
    }
    deleteInputStr() {
        if(this.inputStr) {
            this.inputStr = '';
            this.reset();
            this.init();
        }
    }
    // 
    delaySearch() {
        if (this.searchTimeout) {
            clearTimeout(this.searchTimeout);
        }
        this.searchTimeout = setTimeout(() => {
            this.reset();
            this.init();
            this.searchTimeout = null;
        }, 500)
    }
    reset() {
        this.interestGroupList = [];
        this.page = 1;
        this.searchDone = false;
        this.lastScrollNum = null;
        this.lastDataLength = 0;
    }
    getStaticText(text) {
        if (!this.inputStr) {
            return text;
        }
        let htmltext = this.html2Escape(text.trim());
        let _searchText = this.regExpEscape(this.html2Escape(this.inputStr.trim())) //
        let _text = htmltext.replace(new RegExp(_searchText, 'gi'), `<span class="search-reg">$&</span>`);
        return _text;
    }
    html2Escape(sHtml) {
        return sHtml.replace(/[<>&"]/g, function (c) {
            return { '<': '&lt;', '>': '&gt;', '&': '&amp;', '"': '&quot;' }[c];
        });
    }
    regExpEscape(text) {
        return text.replace(/[\*\.\?\+\$\^\[\]\(\)\{\}\|\\\/]/g, function (c) {
            return { '\*': '\\*', '\.': '\\.', '\?': '\\?', '\+': '\\+', '\$': '\\$', '\^': '\\^', '\[': '\\[', '\]': '\\]', '\(': '\\(', '\)': '\\)', '\{': '\\{', '\}': '\\}', '\|': '\\|', '\/': '\\/', '\\': '\\\\' }[c];
        });
    }
}
